// import '@/assets/item.css';
import HelloWorld from '@/components/HelloWorld.vue';

const FileItemPlugin = () => ({
  install(Vue) {
    const isDuplicateComponent = Vue.options.components[HelloWorld.name];

    if (isDuplicateComponent) {
      if (typeof window !== 'undefined') {
        console.warn('[] Duplicate registration componentName of Item.');
      }
    } else {
      Vue.component(HelloWorld.name, HelloWorld);
    }
  },
});

export { FileItemPlugin as default, HelloWorld };
